<template>
  <div id="app">
    <el-container>
      <el-header>
        <div id="headerLogo"></div>
      </el-header>
      <el-main>
        <!-- <router-view/> -->
        <el-row type="flex" justify="center">
          <ul class="headTip">
            <li>TESTV铁丝站展示铁丝整理TESTV投稿视频。</li>
            <li>目前只归纳<a class="hl-a" href="https://space.bilibili.com/11336264/video" target="_blank">“TESTV官方频道”B站账号</a>的视频投稿。</li>
            <!-- <li>截至目前共收纳{{videoList.length}}个视频。</li> -->
          </ul>
        </el-row>
        <el-row v-show="loading" style="height: 600px" type="flex" justify="center" align="middle">
          <i class="el-icon-loading" style="color: #fefdfc;font-size:36px"></i>
        </el-row>
        <el-row v-show="!loading">
          <el-col :xs="24" :sm="18" v-show="searching">
            <ul>
              <li class="video-item" v-for="item in searchResult" :key="item.aid">
                <a class="hl-a" :href="'https://www.bilibili.com/av' + item.aid" target="_blank">av{{item.aid}} {{item.title}}</a> <br> <span class="video-subtext">系列:{{item.series_type}} | 类型:{{item.video_type}} | 体验:{{item.test_subject}} | 相关品牌:{{item.test_brand}} | B站分区ID:{{item.bili_typeid}} | {{showDate(item.publish_time)}}</span>
              </li>
            </ul>
          </el-col>
          <el-col :xs="24" :sm="searching ? 6 : 24">
            <el-row class="mainRow" :gutter="16">
              <el-col class="mainCol" :xs="24" :sm="searching ? 24 : 8">
                <div><span class="hl-a series-title" @click="doSearchSt('正片')">正片</span><span class="series-title-count subtext">{{seriesType.正片}}</span></div>
                <div>
                  <div class="hl-tag" v-for="item in videoType.正片" v-bind:key="item.n"><span class="hl-a" style="font-size:16px" @click="doSearchVt(item.n)">{{item.n}}</span> <span class="subtext">{{item.c}}</span></div>
                </div>
              </el-col>
              <el-col class="mainCol" :xs="24" :sm="searching ? 24 : 8">
                <div><span class="hl-a series-title" @click="doSearchSt('BBT')">BBT</span><span class="series-title-count subtext">{{seriesType.BBT}}</span></div>
                <div>
                  <div class="hl-tag" v-for="item in videoType.BBT" v-bind:key="item.n"><span class="hl-a" style="font-size:16px" @click="doSearchVt(item.n)">{{item.n}}</span> <span class="subtext">{{item.c}}</span></div>
                </div>
              </el-col>
              <el-col class="mainCol" :xs="24" :sm="searching ? 24 : 8">
                <div><span class="hl-a series-title" @click="doSearchSt('其它')">其它</span><span class="series-title-count subtext">{{seriesType.其它}}</span></div>
                <div>
                  <div class="hl-tag" v-for="item in videoType.其它" v-bind:key="item.n"><span class="hl-a" style="font-size:16px" @click="doSearchVt(item.n)">{{item.n}}</span> <span class="subtext">{{item.c}}</span></div>
                </div>
              </el-col>
            </el-row>
            <el-row class="mainRow" :gutter="16">
              <el-col class="mainCol" :xs="24" :sm="searching ? 24 : 12">
                <div><span class="hl-a series-title">体验项目</span><span class="series-title-count subtext">{{Object.keys(testSubject).length}}</span></div>
                <div>
                  <div class="hl-tag" v-for="item in testSubject" v-bind:key="item.n"><span class="hl-a" style="font-size:16px" @click="doSearchTs(item.n)">{{item.n}}</span> <span class="subtext">{{item.c}}</span></div>
                </div>
              </el-col>
              <el-col class="mainCol" :xs="24" :sm="searching ? 24 : 12">
                <div><span class="hl-a series-title">体验品牌</span><span class="series-title-count subtext">{{Object.keys(testBrand).length}}</span></div>
                <div>
                  <div class="hl-tag" v-for="item in testBrand" v-bind:key="item.n"><span class="hl-a" style="font-size:16px" @click="doSearchTb(item.n)">{{item.n}}</span> <span class="subtext">{{item.c}}</span></div>
                </div>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-main>
      <el-footer>
        <el-row type="flex" justify="center">
          <ul class="headTip">
            <li>关于我：一位默默无为的小铁丝，正在往脱离白嫖的道路上努力着。</li>
            <li>网站暂时部署在<a href="https://github.com/congHu/conghu.github.io" target="_blank">Git仓库</a>
            <li><a href="https://space.bilibili.com/11336264/video" target="_blank">前往“TESTV官方频道”的B站空间</a></li>
            <li>都看到这里了，要不给我<a href="./static/quantum_coffee.html">买杯咖啡</a>？</li>
          </ul>
        </el-row>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      loading: true,
      videoList: [],
      seriesType: {},
      videoType: {},
      testSubject: [],
      testBrand: [],
      searching: false,
      searchResult: [],
      searchResultPage: 1
    }
  },
  mounted () {
    this.$http({
      method: 'get',
      url: './static/testv.csv'
    }).then((res) => {
      let vlist = []
      let paramNames = []

      var sType = {}
      var vType = {}
      var tSub = {}
      var tBrand = {}

      res.data.split('\n').forEach((v, i) => {
        if (v === '') return
        if (i === 0) paramNames = v.split(',')
        else {
          let vobj = {}
          v.split(',').forEach((val, ii) => {
            vobj[paramNames[ii].trim()] = val
          })
          vlist.push(vobj)
          let series = vobj.series_type// === '' ? '未分类' : vobj.series_type
          if (sType.hasOwnProperty(series)) {
            sType[series] += 1
          } else {
            sType[series] = 1
            vType[series] = {}
          }

          vobj.video_type.split('|').forEach(s => {
            if (s === '') s = '未分类'
            if (vType[series].hasOwnProperty(s)) {
              vType[series][s] += 1
            } else {
              vType[series][s] = 1
            }
          })

          vobj.test_subject.split('|').forEach(s => {
            if (s === '') return
            if (tSub.hasOwnProperty(s)) {
              tSub[s] += 1
            } else {
              tSub[s] = 1
            }
          })

          vobj.test_brand.split('|').forEach(s => {
            if (s === '') return
            if (tBrand.hasOwnProperty(s)) {
              tBrand[s] += 1
            } else {
              tBrand[s] = 1
            }
          })
        }
      })
      this.videoList = vlist
      this.seriesType = sType

      let vTypeObjAry = {}
      for (const key in vType) {
        const vt = vType[key]
        vTypeObjAry[key] = Object.keys(vt).map(k => {
          return {n: k, c: vt[k]}
        })
        vTypeObjAry[key].sort((a, b) => b.c - a.c)
      }
      let tSubAry = Object.keys(tSub).map(k => {
        return {n: k, c: tSub[k]}
      })
      let tBrandAry = Object.keys(tBrand).map(k => {
        return {n: k, c: tBrand[k]}
      })
      this.videoType = vTypeObjAry
      this.testSubject = tSubAry.sort((a, b) => b.c - a.c)
      this.testBrand = tBrandAry.sort((a, b) => b.c - a.c)

      this.loading = false
      // console.log(this.seriesType)
      // console.log(this.videoType)
      // console.log(this.testSubject)
      // console.log(this.testBrand)

      // console.log(this.videoList.filter(vobj => vobj.series_type === 'BBT'))
    })
  },
  methods: {
    doSearchSt (tag) {
      this.searching = true
      this.searchResult = this.videoList.filter(o => o.series_type === tag)
      // console.log(this.searchResult)
      document.documentElement.scrollTop = 0
    },
    doSearchVt (tag) {
      this.searching = true
      this.searchResult = this.videoList.filter(o => o.video_type === tag)
      document.documentElement.scrollTop = 0
    },
    doSearchTs (tag) {
      this.searching = true
      this.searchResult = this.videoList.filter(o => o.test_subject.indexOf(tag) >= 0)
      document.documentElement.scrollTop = 0
    },
    doSearchTb (tag) {
      this.searching = true
      this.searchResult = this.videoList.filter(o => o.test_brand.indexOf(tag) >= 0)
      document.documentElement.scrollTop = 0
    },
    showDate (time) {
      let date = new Date(parseInt(time) * 1000)
      let m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      let d = date.getDate()
      d = d < 10 ? '0' + d : d
      return date.getFullYear() + '-' + m + '-' + d
    }
  }
}
</script>

<style>
i.fa {
  vertical-align: baseline;
  margin-right: 10px;
}
body{
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  background: #1e1e1e;
  color: #606266;
}
a{
  /* color: #909399; */
  color: inherit;
  transition: all .3s;
}
a:hover{
  color: #dfc48a;
}
.hl-a{
  font-weight: 500;
  text-decoration: none;
  cursor: pointer;
  transition: all .3s;
}
.hl-a:hover{
  color: #dfc48a;
}
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#headerLogo{
  width: 100%;
  height: 100%;
  background: url('./assets/logo.png') no-repeat center ;
}
.el-link.el-link--default:hover{
  color: #dfc48a;
}
.headTip{
  font-size: 14px;
  margin-top: 16px;
  /* text-align: center; */
  display: inline-block;
}
.series-title{
  /* color: #fefdfc; */
  font-size: 32px;
}
.series-title.el-link{
  color: inherit;
}
.series-title-count{
  margin-left: 8px;
}
.mainRow{
  /* min-height: 600px; */
  margin-top: 16px;
}
.mainCol{
  transition: all .3s;
  color: #fefdfc;
  opacity: .5;
  margin-top: 8px;
}
.mainCol:hover{
  opacity: 1;
}
.mainCol .el-link{
  color: inherit;
}
.subtext{
  color: #606266;
}
.el-tag{
  background: none;
  border: none;
  color: inherit;
}
.video-item{
  color: #fefdfc;
  opacity: .5;
  transition: all .3s;
  padding: 4px 0;
}
.video-item .el-link{
  font-size: 16px;
  color: #fefdfc;
}
.video-item:hover{
  opacity: 1;
}
.video-subtext{
  color: #909399;
  font-size: 14px;
}
.hl-tag{
  display: inline-block;
  padding: 8px;
  font-size: 13px;
}
</style>



